import Vue from 'vue'
import Router from 'vue-router'
import AutoTools from '@/viewers/autoCalc/AutoTools'
import CalcAnalyse from '@/viewers/autoCalc/CalcAnalyse'
import Card from '@/viewers/businessOM/Card'
import CardAdmin from '@/viewers/businessOM/CardAdmin'
import Home from '@/viewers/home/Home'
import MyWork from '@/viewers/work/MyWork'
import MyWorkMain from '@/viewers/work/myWorks/MyWorkMain'
import MyWorkDetail from '@/viewers/work/myWorks/MyWorkDetail'
import WorkAdmin from '@/viewers/work/WorkAdmin'
import NavBar from '@/components/nav/NavBar'
import Header from '@/components/header/header'
import Footer from '@/components/footer/footer'
import Login from '@/viewers/login/login'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: Home
      },
      meta: {
        login: true
      }
    },
    {
      path: '/myWork',
      name: 'MyWork',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: MyWork
      },
      meta: {
        login: true
      },
      children: [
        {
          path: '/myWork',
          component: MyWorkMain,
          meta: {
            login: true
          },
          name: 'MyWorkMain'
        },
        {
          path: '/myWork/myWorkDetail/:id',
          component: MyWorkDetail,
          meta: {
            login: true
          },
          name: 'MyWorkDetail'
        }
      ]
    },
    {
      path: '/workAdmin',
      name: 'WorkAdmin',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: WorkAdmin
      },
      meta: {
        login: true
      }
    },
    {
      path: '/cardsAdmin',
      name: 'CardAdmin',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: CardAdmin
      },
      meta: {
        login: true
      }
    },
    {
      path: '/cards',
      name: 'Card',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: Card
      },
      meta: {
        login: true
      }
    },
    {
      path: '/calcAnalyse',
      name: 'CalcAnalyse',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: CalcAnalyse
      },
      meta: {
        login: true
      }
    },
    {
      path: '/autoTools',
      name: 'AutoTools',
      components: {
        nav: NavBar,
        footer: Footer,
        header: Header,
        default: AutoTools
      },
      meta: {
        login: true
      }
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some((item) => item.meta.login)) {
    let infoStr = router.app.$cookieStore.getCookie('loginInfo')
    let info = JSON.parse(infoStr)
    if (info && info.login) {
      next()
    } else {
      console.info(to.path)
      router.push({
        path: '/login',
        query: {
          redirect: to.path.slice(1)
        }
      })
    }
  } else {
    next()
  }
})

export default router



