<template>
  <header class="header">
    <span style="float:left;">河北证联嘉悦信息技术有限公司</span>
    <a @click="loginOut" style="float:right;color:#fff;">退出</a>
    <span style="float:right;margin-right:20px;">{{userName}}</span>
  </header>
</template>
<script>
export default {
  name: 'Header',
  data () {
    return {
      userName: '',
      isLogin: false
    }
  },
  created () {
    let infoStr = this.$cookieStore.getCookie('loginInfo')
    let info = JSON.parse(infoStr)
    this.isLogin = info.login
    this.userName = info.userName
  },
  methods: {
    loginOut () {
      this.$cookieStore.delCookie('loginInfo')
      console.info('cookie:'+this.$cookieStore.getCookie('loginInfo'))
      this.$router.push('/login')
    }
  }
}
</script>



