<template>
  <div class="">
    <div class="contentTitle">{{msg}}</div>
      <div class="content">
        <table>
          <thead>
          <tr>
            <th>id</th>
            <th>工作名称</th>
            <th>完成状态</th>
            <th>开始时间</th>
            <th>完成时间</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in works" @click="seeDetail (item.id)" :key="item.name">
            <td>{{item.id}}</td>
            <td>{{item.name}}</td>
            <td>{{item.status}}</td>
            <td>{{item.startTime}}</td>
            <td>{{item.endTime}}</td>
          </tr>
          </tbody>
        </table>
      </div>
  </div>
</template>

<script>
export default {
  name: 'MyWorkMain',
  data () {
    return {
      msg: '我的工作页面',
      works: []
    }
  },
  created: function () {
    let url = this.getAPI().workList
    this.$http.get(url).then((response) => {
      console.info(response.data.data)
      this.works = response.data.data
    }).catch((error) => {
      console.log('请求失败')
    })
  },
  methods: {
    seeDetail (id) {
      let pathString = '/myWork/myWorkDetail/' + id
      this.$router.push({path: pathString})
    }
  }
}
</script>
<style scoped>
  table{
    width:100%;
    border-collapse: collapse;
  }
  td,th{
    border:1px solid #e3e3e3;
    line-height:30px;
    text-align:center;
  }
</style>



