<template>
  <div style="text-algin:center;">
    <div class="contentTitle">{{msg}}   我的Id是：{{ $route.params.id }}</div>
    <button style="margin:20px 40%;width:100px;height:40px;" type="button" @click="returnMainPage">返回</button>
  </div>
</template>

<script>
export default {
  name: 'MyWorkDetail',
  data () {
    return {
      msg: '我是用户详情页面'
    }
  },
  methods: {
    returnMainPage: function () {
      this.$router.push({path: '/myWork'})
    }
  },
  created: function () {
    console.info(this.$route.params)
  }
}
</script>



