<template>
  <div class="navSection">
    <ul class="navBox">
      <li v-for = "(item, i) in bigNav" :key="item.title"><a @click="tabHandle (i)" :class="{bigActive:i === index}" endTime>{{item.title}}</a></li>
    </ul>
      <nav class="linkBox" v-for="(item, j) in bigNav" :key="item.title" :style="{display: j === index ? 'block' : 'none'}">
        <li v-for="option in item.childNav" :key="option.name"><router-link  :to="option.to" >{{option.name}}</router-link></li>
      </nav>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  data () {
    return {
      index: 0,
      bigNav: [{
        title: '工作台',
        to: '/home',
        childNav: [{
          to: '/home',
          name: '个性首页'
        }, {
          to: '/myWork',
          name: '我的工作'
        }, {
          to: '/workAdmin',
          name: '工作管理'
        }]
      }, {
        title: '业务运维',
        to: '/cards',
        childNav: [{
          to: '/cards',
          name: '业务卡片'
        }, {
          to: '/cardsAdmin',
          name: '卡片管理'
        }]
      }, {
        title: '智能分析',
        to: '/calcAnalyse',
        childNav: [{
          to: '/calcAnalyse',
          name: '统计分析'
        }, {
          to: '/autoTools',
          name: '智能工具'
        }]
      }]
    }
  },
  methods: {
    tabHandle (i) {
      this.index = i
      let pathString = this.bigNav[i].childNav[0].to
      this.$router.push({path: pathString})
    }
  },
  watch: {
    '$route' (to, from) {
      let pathString = this.$router.history.current.path;
      this.bigNav.forEach((item, i) => {
        item.childNav.forEach((v, m) => {
          if (v.to === pathString) {
            this.index = i
          }
        })
      })
    }
  },
  created: function () {
    let pathString = this.$router.history.current.path
    if (pathString === '/') {
      this.$router.push({path: '/home'})
    }
    this.bigNav.forEach((item, i) => {
      item.childNav.forEach((v, m) => {
        if (v.to === pathString) {
          this.index = i
        }
      })
    })
  }
}
</script>



