<template>
  <div class="loginBox">
    <div class="login">
      <h2 class="center marginBottom20">登陆</h2>
      <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
        <FormItem label="用户名" prop="userName">
          <Input type="text" v-model="formCustom.userName"></Input>
        </FormItem>
        <FormItem label="密码" prop="passwd">
          <Input type="password" v-model="formCustom.passwd"></Input>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleSubmit('formCustom')" style="width:200px;">登陆</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码！'));
      } else if (value !== "123456") {
        callback(new Error('密码错误请重新输入！'));
      } else {
        callback();
      }
    };
    const validateUserName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名！'));
      } else if (value !== "admin") {
        callback(new Error('用户名错误请重新输入！'));
      } else {
        callback();
      }
    };
    return {
      formCustom: {
        passwd: '',
        userName: '',
      },
      ruleCustom: {
        passwd: [
          { validator: validatePass}
        ],
        userName: [
          { validator: validateUserName}
        ]
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          let user = this.formCustom.userName
          //let passWord = this.$refs.passwd.value;
          //alert(user)
          let cookObj = {
            login: true,
            userName: user
          }
          let cookStr = JSON.stringify(cookObj)
          this.$cookieStore.setCookie('loginInfo', cookStr, 1)
          this.$Message.success('登陆成功!');
          let redirect = this.$route.query.redirect
          if (!redirect) {
            redirect = 'home'
          }
          this.$router.push({
            path: '/' + redirect
          })
        } else {
          this.$Message.error('登陆失败!');
        }
      })
    }
  }
}
</script>
<style scoped>
  @import "../../assets/css/login.css";
</style>



